            [Parsha.PEKUDEI]: 'Pekudei',
            [Parsha.VAYIKRA]: 'Vayikra',
            [Parsha.TZAV]: 'Tzav',
            [Parsha.SHMINI]: 'Shmini',
            [Parsha.TAZRIA]: 'Tazria',
            [Parsha.METZORA]: 'Metzora',
            [Parsha.ACHREI_MOS]: 'Achrei Mos',
            [Parsha.KEDOSHIM]: 'Kedoshim',
            [Parsha.EMOR]: 'Emor',
