            [Parsha.REEH]: 'Re\'eh',
            [Parsha.SHOFTIM]: 'Shoftim',
            [Parsha.KI_SEITZEI]: 'Ki Seitzei',
            [Parsha.KI_SAVO]: 'Ki Savo',
            [Parsha.NITZAVIM]: 'Nitzavim',
            [Parsha.VAYEILECH]: 'Vayeilech',
            [Parsha.HAAZINU]: 'Ha\'Azinu',
            [Parsha.VZOS_HABERACHA]: 'Vezos Habracha',
